section.cards-library {
  margin: 40px 0 0;
}

.container-library {
  width: 100%;
  margin: auto;
  padding: 0 20px;
}

.dropdown-library {
  margin: 0 0 16px;
  display: inline-block;
  position: relative;
}

.dropdown-toggle-library {
  width: 173px;
  cursor: pointer;
  border: 1px solid var(--white);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  padding: 10px 56px 10px 16px;
  font-size: 14px;
  line-height: 16px;
}

.dropdown-menu-library {
  width: 172px;
  border: 1px solid var(--white);
  z-index: 5000;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  display: none;
  position: absolute;
}

.dropdown-item-library {
  cursor: pointer;
  border-bottom: 1px solid var(--white);
  width: 100%;
  padding: 14px 66px 10px 20px;
  font-size: 14px;
  line-height: 20px;
}

.dropdown-item-library:hover, .dropdown-item-library:active {
  color: var(--orange);
}

.dropdown-item-library:last-child {
  border: none;
}

.dropdown-toggle-library:before {
  content: "";
  width: 16px;
  height: 16px;
  opacity: 10;
  fill: var(--white);
  background-image: url("chevron-down.168da858.svg");
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: 13px;
  right: 14px;
}

.dropdown-toggle-library.rotated:before {
  transform: rotate(180deg);
}

.dropdown-item-library.\.dropdown-item-library-color {
  color: var(--orange);
}

.load, .search-more {
  height: 38px;
  border: 1px solid var(--orange);
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 74px;
  align-items: center;
  margin: 24px auto 40px;
  padding: 12px 24px;
  font-family: Roboto-500, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  display: flex;
}

.search-more:hover, .load:hover {
  color: var(--orange);
  border: 1px solid var(--orange);
  background-color: rgba(0, 0, 0, 0);
  background-image: none;
}

.movies-gallery-catalog, .notify-message, .empty-gallery {
  text-align: center;
}

